import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';

import { API_EVENT, HOST_API } from '../../config';

import useLocales from '../../hooks/useLocales';

import axios from '../../utils/axios';
import { fDescriptionTypeDate } from '../../utils/formatTime';

import Page from '../../components/Page';
import PageBanner from '../../components/wrappers/PageBanner';
import EventSearchComponent from '../../components/form/EventSearchComponent';
import GradientBorderWrapper from '../../components/wrappers/GradientBorderWrapper';
import Chip from '../../components/Chip';

export default function MyTickets() {
    const { t } = useLocales();
    const navigate = useNavigate();
    const [myEvents, setMyEvents] = useState([]);

    useEffect(() => {
        axios.get(API_EVENT.getMyTicket).then((res) => {
            if (res.status === 200) setMyEvents(res.data.data);
        });
    }, []);

    return (
        <Page title={t('title.tickets')}>
            <PageBanner>
                <div className="container p-4 mb-8">
                    <div className="w-full justify-center flex flex-col mb-12">
                        <h4 className="text-4xl md:text-[64px]">{t('title.tickets')}</h4>
                        <p className="text-stone-400 mt-4">
                            {t(`description.${myEvents.length ? 'my_tickets' : 'empty_ticket'}`)}
                        </p>
                    </div>
                    <div className="flex flex-col gap-4 w-full mb-8">
                        {myEvents.length === 0 && (
                            <div className="max-w-2xl mb-4">
                                <EventSearchComponent />
                            </div>
                        )}
                        <div className="flex flex-col gap-4 w-full mb-8">
                            <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                                {myEvents.map((item, key) => (
                                    <GradientBorderWrapper
                                        key={key}
                                        onClick={() =>
                                            navigate(`/ticket/list/${item._id}`, { state: { type: 'my-ticket' } })
                                        }
                                        className="flex px-6 p-[19px] justify-between bg-base-200 rounded-xl cursor-pointer hover:bg-secondary/50"
                                    >
                                        <div className="flex gap-6">
                                            <img
                                                src={`${HOST_API}uploads/ticket_avatar/default-ticket.svg`}
                                                className="w-16 h-20 rounded-xl "
                                                alt=""
                                            />
                                            <div className="flex items-stretch justify-between flex-col w-full">
                                                <label className={`text-[19px] capitalize ${item.removed && "text-stone-400"}`}>{item?.name}</label>
                                                <div>
                                                    <span className="text-sm text-stone-400">
                                                        {fDescriptionTypeDate(item?.dateTime)}
                                                    </span>
                                                    <br />
                                                    <div className="flex justify-between">
                                                        <span className='text-base text-stone-400' >{`${item?.place}, ${item?.location.city.name} ${item?.location.city.countryCode} `}</span>
                                                        {item.removed && <Chip label={t('label.removed')} color="red-500" />}
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </GradientBorderWrapper>
                                ))}
                            </div>
                        </div>
                    </div>
                </div>
            </PageBanner>
        </Page>
    );
}
